@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Roboto', sans-serif;
}

.App-logo {
  height: 200px;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .names {
    -webkit-animation: Name-Enter 1 2s linear;
            animation: Name-Enter 1 2s linear;
    text-align: left;
    letter-spacing: 5px;
  }
  .lastNames {
    -webkit-animation: Last-Name-Enter 1 2s linear;
            animation: Last-Name-Enter 1 2s linear;
    text-align: left;
    letter-spacing: 5px;
  }
}

.Name-Header{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left !important;
  line-height: 0 !important;
}

.Menu-Header{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: auto;
}

.Light-Section{
  background-color: #383838;
  padding: 100px;
}

.hor-flex{
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}

.Dark-Section{
  background-color: #242424;
  min-height: 1000px;
  width: 100%;
}

.Menu-Text{
  font-weight: 300;
  font-size: 25px;
  margin: auto;
  min-width: 150px;
  text-align: left;
  text-decoration: none;
  color: white;
}

.App-header {
  background-color: #242424;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


@-webkit-keyframes Name-Enter {
  from {
    letter-spacing: -10px;
  }
  to {
    letter-spacing: 5px;
  }
}


@keyframes Name-Enter {
  from {
    letter-spacing: -10px;
  }
  to {
    letter-spacing: 5px;
  }
}

@-webkit-keyframes Last-Name-Enter {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes Last-Name-Enter {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
